import React, { useState, useEffect } from "react"
import { PageProps, Link, graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

import { makeStyles } from '@material-ui/core/styles';

import storeData from '../resource/StoreData';

const useStyles = makeStyles((theme) => ({
	iframe: {
		width: '100%',
		height: 'calc(100% - 57px)',
		margin: '0 auto',
		border: 'none',
	},
}));

type Data = {
	site: {
	  siteMetadata: {
		title: string
	  }
	}
  }

const OrderLayout = ({ location, title, children }) => {

	return (
		<Layout location={location} title={title}>
			<div
				style={{
					height: "100vh",
				}}
				className="orderLayout"
			>
				<main
					style={{
						padding: `0`,
						height: "100%",
					}}
				>
					{children}
				</main>
			</div>
		</Layout>
	)
}

const OrderPage = ({ data, location }: PageProps<Data>) => {
	const siteTitle = data.site.siteMetadata.title

	const classes = useStyles();

	const [selectedStore, setSelectedStore] = useState(storeData[0]);

	useEffect(() => {
		let currentStore = window.localStorage.getItem("store");
		if (currentStore === null) {
			currentStore = JSON.stringify(storeData[0]);
			window.localStorage.setItem("store", currentStore);
		}
		currentStore = JSON.parse(currentStore);
		setSelectedStore(currentStore);
	}, []);

	return (
		<OrderLayout location={location} title={siteTitle}>
			<SEO title="Order" />
			<iframe
				className={classes.iframe}
				title="Online order"
				src={selectedStore.url}
			></iframe>
		</OrderLayout>
	)
}

export default OrderPage;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
